@import '~assets/styles/h3Imports';

.root {
  @media (max-width: $max-tablet-size) {
    --modal-max-width: #{em(550)};

    padding: var(--modal-spacing-tablet);
  }
}

.top-content-wrapper {
  margin-bottom: em(16);
  @media (max-width: $max-phone-size) {
    margin-bottom: em(24);
  }
}

.bottom-content-wrapper {
  align-items: center;
  display: flex;
  margin-bottom: em(16);
  margin-top: em(16);

  @media (max-width: $max-phone-size) {
    margin-bottom: em(24);
  }
}

.bottom-content-text {
  flex: 5;
}

.image {
  flex: 1;
}

.paragraph {
  line-height: 1.6;
}

.paragraph:first-child {
  margin-top: 0;
}

.asterisk {
  color: var(--interface-error);
}

.phone-number {
  display: flex;
  flex-direction: column;
  margin-bottom: em(4);
  width: em(348);
}

.title {
  font-size: em(27);
}

.sub-title {
  @include paragraph;

  font-weight: 700;
  margin-bottom: em(8);
}

.submit-button {
  --button-width: 100%;

  margin-top: em(24);
}

[data-layout='mobile'] {
  .header {
    margin: em(24) 0;
  }
}
